.home-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  height: 1.333333rem;
  background-color: rgb(233, 32, 61);
  color: #fff;
  font-size: 0.426667rem;
}

.home-header-left {
  width: 2.293333rem;
  text-align: center;
}

.home-header-left .city-name {
  font-size: 0.426667rem;
  color: #d3eee9;
}

.home-header-center {
  width: 6.293333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: #fff;
  border-radius: 0.4rem;
}

.home-header-center input {
  width: 5.333333rem;
  border: none;
  text-indent: 0.266667rem;
  font-size: 0.373333rem !important;
  font-weight: normal;
}

.home-header-center i {
  position: relative;
  top: 0.08rem;
  margin-left: 0.266667rem;
  font-size: 0.533333rem;
  color: #ccc !important;
}

.home-header-right {
  width: 1.413333rem;
  text-align: center;
}


