body {
  margin: 0;
  padding: 0;
  background-color: #eeeeee;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "微软雅黑","Times New Roman",Georgia,Serif;
}

a {
  text-decoration: none;
}

img {
  border: 0;
}

input {
  outline: none;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.clear-fix:after {
  display: table;
  content: '';
  clear: both;
}

.a {
  font-size: 0.213333rem;
}


