.home-advertisement {
  background-color: #fff;
}

.home-advertisement p {
  padding: 0.346667rem 0.4rem;
  font-size: 0.426667rem;
  font-weight: bold;
  color: #363233;
}

.home-advertisement ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  list-style: none;
}

.home-advertisement ul li {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 3.333333rem;
  height: 3.466667rem;
  border: 1px solid #eee;
}

.hairlines .home-advertisement ul li {
  border: 0.5px solid #eee;
}

.home-advertisement ul li img {
  width: 100%;
  height: 100%;
  border: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}


