@font-face {
  font-family: 'icomoon';
  src: url('./icomoon.eot?6394vp');
  src: url('./icomoon.eot?6394vp#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?6394vp') format('truetype'),
    url('./icomoon.woff?6394vp') format('woff'),
    url('./icomoon.svg?6394vp#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

